import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js'
import ThreeMeshUI from 'three-mesh-ui'
import dat from 'dat.gui'

window.global = window;
// @ts-ignore
window.Buffer = window.Buffer || require('buffer').Buffer;
global.THREE = require('three')
var createGeometry = require('three-bmfont-text')
var loadFont = require('load-bmfont')

const inputTextoDOM = document.querySelector("input.inputTexto")
const inputNumeroDOM = document.querySelector("input.inputNumero")

const gui = new dat.GUI();

const canvas = document.querySelector('canvas.webgl');
//  console.log(GLTFLoader);
const sizes = {
    width: window.innerWidth,
    height: 600
}


const scene = new THREE.Scene();

const camera = new THREE.PerspectiveCamera(75,sizes.width/sizes.height,0.1,1000);
camera.position.set(0,5,5);
camera.lookAt(new THREE.Vector3(0,20,10))
scene.add(camera);


const axisHelper = new THREE.AxesHelper(2);
scene.add(axisHelper)

//Texturas
const textureLoader = new THREE.TextureLoader();


//MATERIAL
const updateAllMaterials = () =>
{
    scene.traverse((child) =>
    {
        if(child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial)
        {
            // ...

            child.castShadow = true
            child.receiveShadow = true
        }
    })
}
//MESH

const gltfLoader = new GLTFLoader();
let modelo = null;
gltfLoader.load('camiseta_01/scene.gltf',
(gltf)=>{
    // console.log("cargado");
    // console.log(gltf)
    scene.add(gltf.scene);
    gltf.scene.scale.set(0.005,0.005,0.005);
    gltf.scene.position.set(0,-4.0,0);
    gltf.scene.receiveShadow = true;
    gltf.scene.castShadow = true;
    modelo = gltf.scene;
    // console.log(modelo);
    updateAllMaterials();
    // camera.lookAt(new THREE.Vector3(
    //     modelo.position.x,
    //     modelo.position.y ,
    //     modelo.position.z,
    //                     ));

},

);

const plane = new THREE.Mesh(
    new THREE.PlaneGeometry(10,10,4),
    new THREE.MeshStandardMaterial({
       
    })
)
plane.rotation.x = -Math.PI *0.5;
plane.receiveShadow = true;
 scene.add(plane);

//TextMESH UI
const datosCamisa ={
    nombre:'Josue G.Z',
    numero: 7
}
const container = new ThreeMeshUI.Block({
    width: 1.2,
    height: 0.5,
    padding: 0.05,
    justifyContent: 'center',
    alignContent: 'center',
    fontFamily: 'fonts/Roboto-msdf.json',
    fontTexture: 'fonts/Roboto-msdf.png',
    backgroundOpacity:0,
    fontColor:new THREE.Color('#000011')

})
container.position.set( 0, 4.3, -0.85 );
container.rotation.y = Math.PI;
container.rotation.x =  0.1;
scene.add( container );
const textoNombre = new ThreeMeshUI.Text({content: datosCamisa.nombre,
    fontSize: 0.2})
container.add(
		textoNombre

);
var geometry = null;

gui.add(datosCamisa,'nombre').name("nombre").onFinishChange(()=>{
    console.log("se ingreso nombre");
    textoNombre.set({content:datosCamisa.nombre})
    geometry.update(datosCamisa.nombre)

});
gui.add(datosCamisa,'numero').name("numero");
//BMFont text
loadFont('fonts/DejaVu-sdf.fnt',
 function(err, font) {
    if (err)
      throw err
    
    
      geometry = createGeometry({
        width: 300,
        align: 'center',
        font: font,
        letterSpacing:1.5
      })
      geometry.update('JOSUE G.Z.')
      var textureLoader = new THREE.TextureLoader();
      textureLoader.load('fonts/DejaVu-sdf.png', function (texture) {
        // we can use a simple ThreeJS material
      var material = new THREE.MeshBasicMaterial({
      map: texture,
      transparent: true,
      color: 0x0000ff
    })

    // now do something with our mesh!
    var mesh = new THREE.Mesh(geometry, material)

    scene.add(mesh);
    mesh.position.set(1.88,4.05,-1.15);
    mesh.rotation.x=3;
    mesh.rotation.y=Math.PI;
    mesh.scale.set(0.01,0.01,1.0);
    gui.add(mesh.position,'x').min(-10.0).max(10.0).step(0.01);
    gui.add(mesh.position,'y').min(-10.0).max(10.0).step(0.01);
    gui.add(mesh.position,'z').min(-10.0).max(10.0).step(0.01);
    gui.add(mesh.rotation,'x').min(-Math.PI*2).max(Math.PI *2).step(0.001);
    gui.add(mesh.rotation,'y').min(-Math.PI*2).max(Math.PI *2).step(0.001);
    gui.add(mesh.scale,'x').min(0.0).max(10.0).step(0.001);
    gui.add(mesh.scale,'y').min(0.0).max(10.0).step(0.001);
    gui.addColor(mesh.material,'color');
  })
  })
gui.close();
inputTextoDOM.addEventListener('input',actualizarNombre);

function actualizarNombre(valor){
 console.log("Holi"+valor.srcElement.value);
 datosCamisa.nombre = valor.srcElement.value;
 textoNombre.set({content:datosCamisa.nombre})
    geometry.update(datosCamisa.nombre)
}


//LUZ
const luzDirection = new THREE.DirectionalLight("#ffffff",3);
luzDirection.castShadow = true;
luzDirection.position.set(0, 5,  2.25)
luzDirection.shadow.camera.far = 15;
luzDirection.shadow.normalBias = 0.05;
scene.add(luzDirection);

const luzDireccionHelper = new THREE.CameraHelper(luzDirection.shadow.camera);
// scene.add(luzDireccionHelper);

const ambientLight = new THREE.AmbientLight('#ffffff',0.5)
scene.add(ambientLight);
//RENDERER
const renderer = new THREE.WebGLRenderer({
    canvas:canvas,
    antialias: true
});
renderer.setSize(sizes.width,sizes.height);
renderer.render(scene,camera);
renderer.physicallyCorrectLights = true
renderer.shadowMap.enabled = true;
renderer.outputEncoding = THREE.sRGBEncoding
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.toneMapping = THREE.ACESFilmicToneMapping
//ORBIT
const orbitcontrol = new OrbitControls(camera,canvas);
orbitcontrol.enableDamping = true;

orbitcontrol.update();

//TICK
const reloj = new THREE.Clock();
const tick = () =>
{
    const tiempoTranscurrido = reloj.getElapsedTime();
    orbitcontrol.update();
    // console.log(tiempoTranscurrido);
    // if(modelo != null)
    //     camera.lookAt(modelo.position);
    ThreeMeshUI.update();//obligatorio para renderizar
    renderer.render(scene,camera);

    window.requestAnimationFrame(tick);


}
tick();


//Foto
const botonShare = document.querySelector('button.botonShare')
botonShare.addEventListener('click',sacarFoto);
let imgData = null;

function sacarFoto() { 
    console.log("sacando imagen")
    // window.requestAnimationFrame(sacarFoto);
    renderer.render(scene, camera);
  
        imgData = renderer.domElement.toDataURL('image/jpeg',1.0);
        //getImageData = false;
        var link = document.createElement('a');

        link.setAttribute('href', imgData);
        link.setAttribute('target', '_blank');
        link.setAttribute('download', "img");
        link.click();
        link.remove();
    
      
}
